// jest.setup.js

// Mock NativeAnimatedHelper pour éviter les erreurs d'Animated dans les tests
jest.mock('react-native/Libraries/Animated/NativeAnimatedHelper', () => ({}));

import { NativeModules } from 'react-native';

// Mock AsyncStorage (très utilisé dans RN)
jest.mock('@react-native-async-storage/async-storage', () =>
  require('@react-native-async-storage/async-storage/jest/async-storage-mock')
);

// Mock Expo font (si tu utilises expo-font, sinon tu peux retirer)
jest.mock('expo-font', () => ({
  useFonts: () => [true],
  isLoaded: () => true,
}));

// Mock Expo haptics (vibration)
jest.mock('expo-haptics', () => ({
  impactAsync: jest.fn(),
}));

// Mock react-native-reanimated pour éviter les erreurs dans les tests
jest.mock('react-native-reanimated', () => {
  const Reanimated = require('react-native-reanimated/mock');

  // Patch pour éviter des erreurs supplémentaires
  Reanimated.default.call = () => {};

  return Reanimated;
});

// Mock des Settings natives (pour éviter erreurs TurboModuleRegistry)
jest.mock('react-native/Libraries/Settings/Settings', () => ({
  get: jest.fn(() => 'light'),
  addEventListener: jest.fn(),
  removeEventListener: jest.fn(),
}));

// Mock NativeModules.SettingsManager (simule locale)
NativeModules.SettingsManager = NativeModules.SettingsManager || {
  settings: { AppleLocale: 'en_US' },
};

// Polyfill global pour setImmediate / clearImmediate (parfois manquant)
if (typeof setImmediate === 'undefined') {
  global.setImmediate = (fn, ...args) => setTimeout(fn, 0, ...args);
}
if (typeof clearImmediate === 'undefined') {
  global.clearImmediate = (id) => clearTimeout(id);
}

// Mock complet du module Animated (si tu utilises Animated manuellement)
jest.mock('react-native/Libraries/Animated/Animated', () => {
  return {
    Value: jest.fn().mockImplementation((val) => ({
      _value: val,
      setValue: jest.fn(),
      addListener: jest.fn(),
      removeListener: jest.fn(),
      removeAllListeners: jest.fn(),
      interpolate: jest.fn(() => ({
        interpolate: jest.fn(),
      })),
    })),
    View: jest.fn(),
    timing: jest.fn(() => ({
      start: (cb) => cb && cb({ finished: true }),
    })),
    spring: jest.fn(() => ({
      start: (cb) => cb && cb({ finished: true }),
    })),
    decay: jest.fn(() => ({
      start: (cb) => cb && cb({ finished: true }),
    })),
    sequence: jest.fn(() => ({
      start: (cb) => cb && cb({ finished: true }),
    })),
    parallel: jest.fn(() => ({
      start: (cb) => cb && cb({ finished: true }),
    })),
    event: jest.fn(),
  };
});
